.mj-grid {
  border: 1px solid #CCCCCC;
}
.mj-grid .dropdown-menu-td {
  text-align: right;
}
.mj-grid .options-col {
  width: 90px;
}
.mj-grid .mj-grid {
  overflow-x: visible;
}
.mj-grid table {
  margin-bottom: 0;
}
.mj-grid table > thead > tr > th {
  border-bottom: none;
}
.mj-grid .list-options {
  float: right;
}
.mj-grid .list-options button {
  padding: 0 5px;
  color: #999;
}
.mj-grid .list-options.no-drp button {
  background-color: transparent;
  border: none;
}
.mj-grid .list-options.no-drp button:first-child {
  margin-right: 2px;
}
.mj-grid .list-options.no-drp button span {
  font-size: 15px;
}
.mj-grid .m-master {
  bXorder-bottom: 1px solid #CCCCCC;
}
.mj-grid .m-master td div.cell-content {
  display: inline;
  position: relative;
}
.mj-grid .m-master td div.cell-content input[type="text"],
.mj-grid .m-master td div.cell-content input[type="number"] {
  height: 30px;
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 0;
}
.mj-grid .m-master td div.cell-content input[type="radio"],
.mj-grid .m-master td div.cell-content input[type="checkbox"] {
  height: 14px;
  width: 14px;
  display: block;
}
.mj-grid .m-master td ul.mj-children {
  padding: 0;
  margin: 0;
  list-style: none;
}
.mj-grid .m-master td ul.mj-children ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.mj-grid .m-master td:first-child ul.mj-children {
  padding-left: 0;
  list-style: none;
  margin: 0;
}
.mj-grid .m-master td:first-child ul.mj-children li {
  position: relative;
}
.mj-grid .m-master td:first-child ul.mj-children ul {
  list-style: none;
  padding-left: 10px;
  margin: 0;
}
.mj-grid .m-master td:first-child ul.mj-children ul li {
  position: relative;
}
.mj-grid .m-master td p {
  margin-bottom: 0;
}
.mj-grid .m-master td a.sub {
  color: #333;
  text-decoration: none;
}
.mj-grid .m-master:hover {
  background-color: #f4f4f4;
}
.mj-grid .m-detail {
  background-color: #FFF;
}
.mj-grid .m-detail .m-master {
  background-color: #FFF;
  border-bottom: none;
}
.mj-grid .m-detail .m-master td {
  position: relative;
}
.mj-grid .m-detail .m-master td:first-child {
  padding-left: 25px;
}
.mj-grid .m-detail .m-master td:first-child:before {
  content: '';
  width: 10px;
  height: 50%;
  position: absolute;
  border-left: 1px solid #d9d9d9;
  top: 0;
  left: 10px;
  border-bottom: 1px solid #d9d9d9;
}
.mj-grid .m-detail .m-detail .m-master td:first-child {
  padding-left: 40px;
}
.mj-grid .m-detail .m-detail .m-master td:first-child:before {
  left: 20px;
}
.mj-grid .m-detail .m-detail .m-detail .m-master td:first-child {
  padding-left: 50px;
}
.mj-grid .m-detail .m-detail .m-detail .m-master td:first-child:before {
  left: 30px;
}
.mj-grid .m-detail > td {
  padding: 0;
  border-top: none;
}
.mj-grid .mj-grid {
  padding: 10px;
  border: none;
}
.mj-grid.no-title {
  padding: 0;
}
.mj-grid.no-title .mj-grid-content > table {
  border: none;
}
.mj-grid.no-title > table {
  border: none;
}
.mj-grid .dropdown.right .dropdown-menu {
  left: auto;
  right: 0;
}
.mj-grid.display-tree .m-master {
  background-color: #fff;
  border-bottom: none;
}
